import { mapMutations } from 'vuex'

const columns = [
  {
    title: 'Birim',
    dataIndex: 'asset',
    key: 'asset',
    sorter: true,
    width: '20%',
    scopedSlots: {
      customRender: 'asset',
    },
  },
  {
    title: 'Kullanlabilir',
    dataIndex: 'free',
    key: 'free',
    width: '15%',
    sorter: true,
    scopedSlots: {
      customRender: 'free',
    },
  },
  {
    title: 'lemde',
    dataIndex: 'locked',
    key: 'locked',
    width: '15%',
    sorter: true,
    scopedSlots: {
      customRender: 'locked',
    },
  },

  {
    title: 'Toplam',
    dataIndex: 'TotalUSD',
    key: 'TotalUSD',
    width: '15%',
    sorter: true,
    scopedSlots: {
      customRender: 'TotalUSD',
    },
  },
  {
    title: 'BTC Karl',
    dataIndex: 'BtcQty',
    key: 'BtcQty',
    width: '15%',
    sorter: true,
    scopedSlots: {
      customRender: 'BtcQty',
    },
  },
  // {
  //   title: 'Eylem',
  //   dataIndex: 'actions',
  //   key: 'actions',

  //   className: 'action-buttons',
  //   scopedSlots: { customRender: 'actions' },
  // },
]

const mobileColumns = [
  {
    title: 'Birim',
    dataIndex: 'asset',
    key: 'asset',
    sorter: true,
    width: '20%',
    scopedSlots: {
      customRender: 'asset',
    },
  },

  {
    title: 'Toplam',
    dataIndex: 'TotalQty',
    key: 'TotalQty',
    width: '15%',
    sorter: true,
    scopedSlots: {
      customRender: 'TotalQty',
    },
  },
]
export default {
  name: 'Price',
  components: {
    TransferPopUp: () => import('@/components/Wallet/TransferPopUp'),
  },
  data() {
    return {
      data: [],
      originalData: [],
      pagination: {
        pageSize: 17,
      },
      searchInput: null,
      loading: false,
      TotalFiatBalanceInBtc: 0,
      totalFiatBalanceInTry: 0,
      columns,
      mobileColumns,
      isMobile: null,
      visible: false,
      isSecret: false,
      secret: '*******',
    }
  },
  mounted() {
    this.asyncData()

    this.isMobile = window.innerWidth < 1000
    window.addEventListener('resize', () => {
      this.isMobile = window.innerWidth < 1000
    })
  },
  methods: {
    changeSecret() {
      this.isSecret = !this.isSecret
    },
    showModal() {
      this.visible = true
    },
    resetCoin() {
      this.data = this.originalData
    },
    ...mapMutations({
      SET_CURRENT_TAB: 'wallet/SET_CURRENT_TAB',
    }),
    handleTableChange(pagination, filters, sorter) {
      
      const pager = { ...this.pagination }
      pager.current = pagination.current
      this.pagination = pager
      this.loading = false
    },
    onInputChange(e) {
      
      this.data = this.originalData
    },
    onChange(e) {
      let isChecked = e.target.checked
      if (isChecked) {
        this.data = filteredData
      } else {
        this.data = this.originalData
      }
    },
    searchCoin() {
      let filteredData = this.data.filter((element) =>
        element.asset.toLowerCase().includes(this.searchInput.toLowerCase())
      )
      this.data = this.searchInput === '' ? this.originalData : filteredData
    },

    async asyncData() {
      try {
        let authHeaders = {
          Authorization: 'Bearer ' + this.$cookies.get('Token'),
        }

        const response = await this.$axios.get(
          this.$config.FINANCE_API_URL + '/GetUserBalance',
          {
            headers: authHeaders,
          }
        )
        
        if (response.data) {
          // TODO: response.data['Message']
          this.loading = false
          this.data = response.data.UserFiatBalance
          this.originalData = response.data.UserFiatBalance
          this.TotalFiatBalanceInBtc = response.data.TotalFiatBalanceInBtc
          this.totalFiatBalanceInTRY = response.data.TotalFiatBalanceInTRY

          
        }
      } catch (e) {
        
        // if ('response' in e) {
        //   const { status } = e.response
        //   if (status == 400) {
        //     return true
        //   }
        // } else {
        //   redirect('/404')
        // }
      }
    },
  },
}
