const columns = [
  {
    title: 'Birim',
    dataIndex: 'asset',
    key: 'asset',
    sorter: true,
    width: '20%',
    scopedSlots: {
      customRender: 'asset',
    },
  },
  {
    title: 'Kullanlabilir',
    dataIndex: 'free',
    key: 'free',
    width: '15%',
    sorter: true,
    scopedSlots: {
      customRender: 'free',
    },
  },
  {
    title: 'lemde',
    dataIndex: 'locked',
    key: 'locked',
    width: '15%',
    sorter: true,
    scopedSlots: {
      customRender: 'locked',
    },
  },

  {
    title: 'Toplam',
    dataIndex: 'TotalUSD',
    key: 'TotalUSD',
    width: '15%',
    sorter: true,
    scopedSlots: {
      customRender: 'TotalUSD',
    },
  },
  {
    title: 'BTC Karl',
    dataIndex: 'BtcQty',
    key: 'BtcQty',
    width: '15%',
    sorter: true,
    scopedSlots: {
      customRender: 'BtcQty',
    },
  },
  {
    title: '',
    dataIndex: 'actions',
    key: 'actions',
    class: 'action-header',

    className: 'action-buttons',
    scopedSlots: { customRender: 'actions' },
  },
]

const mobileColumns = [
  {
    title: 'Birim',
    dataIndex: 'asset',
    key: 'asset',
    sorter: true,
    width: '20%',
    scopedSlots: {
      customRender: 'asset',
    },
  },

  {
    title: 'Toplam',
    dataIndex: 'TotalUSD',
    key: 'TotalUSD',
    width: '15%',
    sorter: true,
    scopedSlots: {
      customRender: 'TotalUSD',
    },
  },
]
export default {
  name: 'Price',
  components: {
    TransferPopUp: () => import('@/components/Wallet/TransferPopUp'),
    TrustPricePopUp: () => import('@/components/Wallet/TrustPricePopUp'),
  },
  data() {
    return {
      data: [],
      originalData: [],
      pagination: {
        pageSize: 17,
      },
      searchInput: null,
      loading: false,
      totalP2PBalanceInBtc: 0,
      securityBalance: 0,
      btcInTurksihLira: 0,
      columns,
      mobileColumns,
      isMobile: false,
      visible: false,
      tVisible: false,
      isSecret: false,
      secret: '*******',
    }
  },
  mounted() {
    this.isMobile = window.innerWidth < 1000

    window.addEventListener('resize', () => {
      this.isMobile = window.innerWidth < 1000
    })

    this.asyncData()
  },
  methods: {
    changeSecret() {
      this.isSecret = !this.isSecret
      
    },
    showModal() {
      this.visible = true
    },
    showModalT() {
      this.tVisible = true
    },
    handleTableChange(pagination, filters, sorter) {
      
      const pager = { ...this.pagination }
      pager.current = pagination.current
      this.pagination = pager
      this.loading = false
    },
    onChange(e) {
      let isChecked = e.target.checked
      if (isChecked) {
        let filteredData = this.data.filter((element) => element.free !== 0)
        this.data = filteredData
      } else {
        this.data = this.originalData
      }
    },
    searchCoin() {
      let filteredData = this.data.filter((element) =>
        element.asset.toLowerCase().includes(this.searchInput.toLowerCase())
      )
      this.data = this.searchInput === '' ? this.originalData : filteredData
    },

    async asyncData() {
      try {
        let authHeaders = {
          Authorization: 'Bearer ' + this.$cookies.get('Token'),
        }

        const response = await this.$axios.get(
          this.$config.FINANCE_API_URL + '/GetUserBalance',
          {
            headers: authHeaders,
          }
        )
        
        if (response.data) {
          // TODO: response.data['Message']
          this.loading = false
          this.data = response.data.UserP2PBalance
          this.originalData = response.data.UserP2PBalance
          this.totalP2PBalanceInBtc = response.data.TotalP2PBalanceInBtc
          this.securityBalance = response.data.SecurityBalance
          this.btcInTurksihLira = response.data.TotalP2PBalanceInTRY
          
        }
      } catch (e) {
        
        // if ('response' in e) {
        //   const { status } = e.response
        //   if (status == 400) {
        //     return true
        //   }
        // } else {
        //   redirect('/404')
        // }
      }
    },
  },
}
